import React, {Component} from 'react';
import {Input, Button, Form, Typography, message} from 'antd';
import '../css/ResetPassword.css';

const {Title} = Typography;

class ResetPassword extends Component {
    onFinish = (values) => {
        const params = {
            "user": values.username,
            "verification": values.check,
            "newPwd": values.password
        };
        fetch('/api/forgetPwd', {
            method: 'post',
            body: JSON.stringify(params),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        }).then(res => res.json())
            .then(res => {
                if (res.code === 1) {
                    message.success("")
                    setTimeout(() => {
                        this.props.history.push('/login')
                    }, 1500)
                } else if (res.code === 0) {
                    message.error("")
                } else {
                    message.error("")
                }
            })
    }

    render() {
        return (
            <>
                <img className={"login_title"} src={"./qlogo.png"} alt={"logo"}/>
                <Form
                    name="normal_reset"
                    className="reset-form"
                    onFinish={this.onFinish}>
                    <Form.Item
                        name="username"
                        label={""}
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                            ({getFieldValue}) => ({
                                validator(_, value) {
                                    if (!value || (getFieldValue('username').length <= 10)) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject(new Error('10'));
                                },
                            }),
                        ]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item
                        name="check"
                        label={"4"}
                        rules={[
                            {
                                required: true,
                                message: '4',
                            },
                            ({getFieldValue}) => ({
                                validator(_, value) {
                                    if (!value || getFieldValue('check').length === 4) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject(new Error('4'));
                                },
                            }),
                        ]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item
                        name="password"
                        label={""}
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                            ({getFieldValue}) => ({
                                validator(_, value) {
                                    if (!value || (getFieldValue('password').length >= 4 && getFieldValue('password').length <= 16)) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject(new Error('4-16'));
                                },
                            }),
                        ]}>
                        <Input.Password/>
                    </Form.Item>
                    <Form.Item
                        name="repassword"
                        label={""}
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                            ({getFieldValue}) => ({
                                validator(_, value) {
                                    if (!value || getFieldValue('password') === value) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject(new Error(''));
                                },
                            }),
                        ]}>
                        <Input.Password/>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit" className="reset-form-button"></Button>
                    </Form.Item>
                </Form>
            </>
        )
    }
}


export default ResetPassword