import React from 'react';
import '../css/Login.css';
import {Form, Input, Button, Typography, message} from 'antd';
import {UserOutlined, LockOutlined} from '@ant-design/icons';

const {Title} = Typography;

class Login extends React.Component {
    onFinish = (values) => {
        const params = {
            "user": values.username,
            "pwd": values.password,
        };
        fetch('/api/login', {
            method: 'post',
            body: JSON.stringify(params),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        }).then(res => res.json())
            .then(res => {
                if (res.code === 1) {
                    message.success("")
                    setTimeout(() => {
                        this.props.history.push('/showallquestionnaire?username=' + values.username)
                    }, 1500)
                } else if (res.code === 0) {
                    message.error("")
                } else {
                    message.error("")
                }
            })
    }

    render() {
        return (
            <>
                <img className={"login_title"} src={"./qlogo.png"} alt={"logo"}/>
                <Form
                    name="normal_login"
                    className="login-form"
                    onFinish={this.onFinish}>
                    <Form.Item
                        name="username"
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}>
                        <Input prefix={<UserOutlined className="site-form-item-icon"/>} placeholder=""/>
                    </Form.Item>
                    <Form.Item
                        name="password"
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}>
                        <Input.Password
                            prefix={<LockOutlined className="site-form-item-icon"/>}
                            type="password"
                            placeholder=""
                        />
                    </Form.Item>
                    <Form.Item>
                        <a href="http://localhost:3000/register"><strong></strong></a>
                        <a className="login-form-forgot" href="http://localhost:3000/resetpassword"></a>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit" className="login-form-button"></Button>
                    </Form.Item>
                </Form>
            </>
        )
    }
}

export default Login