import React, {Component} from 'react';
import {Badge, Descriptions, Modal, Typography} from "antd";
import '../css/ViewQuestionnaireDetail.css'
import timeConversion from '../../../utils/TimeConversion'

const {Title} = Typography;

class ViewQuestionnaireDetail extends Component {
    constructor() {
        super();
        this.state = {
            questionnaire: {
                title: "",
                publisher: "",
                fillerCount: 0,
                qid: "",
                releaseTime: "",
                deadline: "",
                state: 0,
                questions: []
            }
        }
    }

    componentDidUpdate(prevProps: Readonly<P>, prevState: Readonly<S>, snapshot: SS) {
        if (this.props !== prevProps) {
            const Params = {
                "qid": this.props.qid
            };
            if (this.props.qid !== -1) {
                fetch('api/quesContent', {
                    method: 'post',
                    body: JSON.stringify(Params),
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                    },
                }).then(res => res.json())
                    .then(res => {
                        console.log(res)
                        if (res.code !== 0) {
                            const get = res.data.data[0];
                            this.setState({
                                questionnaire: {
                                    title: get.title,
                                    publisher: get.author,
                                    fillerCount: get.total,
                                    qid: get.qid,
                                    releaseTime: get.status === 0 ? "" : get.start_time,
                                    deadline: get.time,
                                    status: get.status === 0 ? "" : (get.status === 1 ? "" : ""),
                                    questions: get.ask_list.map((list) => {
                                        return ({
                                            subject: list.ask,
                                            type: list.type === 1 ? "" : (list.type === 2 ? "" : ""),
                                            isNecessary: list.isNecessary,
                                            options: list.choice_list.map((choice) => {
                                                return (choice.content)
                                            })
                                        })
                                    })
                                }
                            })
                        }
                    })
            }
        }
        console.log("questions-", this.state.questionnaire.questions);
    }

    render() {
        const questionnaire = this.state.questionnaire
        if (questionnaire.title === "") {
            return null
        } else {
            return (
                <Modal
                    visible={this.props.modalVisible}
                    onOk={() => this.props.handleOk(questionnaire.status, questionnaire.qid)}
                    onCancel={this.props.handleCancel}
                    okText={questionnaire.status === "" ? "" : (questionnaire.status === "" ? "" : "")}
                    cancelText={""}
                    closable={true}
                    width={1000}>
                    <Descriptions title="" column={2} layout={"horizontal"} bordered>
                        <Descriptions.Item label="">{questionnaire.title}</Descriptions.Item>
                        <Descriptions.Item label="">{questionnaire.publisher}</Descriptions.Item>
                        <Descriptions.Item label="">{questionnaire.fillerCount}</Descriptions.Item>
                        <Descriptions.Item label="ID">{questionnaire.qid}</Descriptions.Item>
                        <Descriptions.Item
                            label="">{questionnaire.releaseTime === "" ? "" : timeConversion(questionnaire.releaseTime)}</Descriptions.Item>
                        <Descriptions.Item
                            label="">{timeConversion(questionnaire.deadline)}</Descriptions.Item>
                        <Descriptions.Item label="" span={2}>
                            {questionnaire.status === "" ?
                                <Badge status="processing" text=""/> : (questionnaire.status === "" ?
                                    <Badge status="default" text=""/> : <Badge status="warning" text=""/>)}
                        </Descriptions.Item>
                    </Descriptions>
                    <Descriptions bordered>
                        <Descriptions.Item label="">
                            {questionnaire.questions.map((question, questionID) => {
                                return (
                                    <div key={questionID}>
                                        <Title
                                            level={5}>{questionID + 1 + ". " + question.subject + " [" + question.type + "] "}<span
                                            className={"view_detail_span"}>{(question.isNecessary === true ? "*" : "")}</span></Title>
                                        <p>{question.type === "" ? null : question.options.map((option, optionID) => {
                                            return <span key={optionID}>
                                            <span><strong>{String.fromCharCode(65 + optionID)}.</strong>&nbsp;{option}&nbsp;&nbsp;</span>
                                            <br/>
                                        </span>
                                        })}</p>
                                        <br/>
                                    </div>
                                )
                            })}
                            <br/>
                        </Descriptions.Item>
                    </Descriptions>
                </Modal>
            )
        }
    }
}

export default ViewQuestionnaireDetail